const originalStyleList = require('./style-list')
const tool = require('../util/tool')
const Pool = require('../util/pool')
const cache = require('../util/cache')

const pool = new Pool()
let styleList = [].concat(originalStyleList)
let isInitStyleList = false

/**
 * æ ·å¼åè½¬è¿æ¥ç¬¦
 */
function toDash(name) {
    if (name.indexOf('webkit') === 0) name = name.replace('webkit', 'Webkit')
    return tool.toDash(name)
}

/**
 * æ ·å¼åè½¬é©¼å³°
 */
function toCamel(name) {
    if (name.indexOf('-webkit-') === 0) name = name.replace('-webkit-', 'webkit-')
    return tool.toCamel(name)
}

/**
 * è§£ææ ·å¼ä¸²
 */
function parse(styleText) {
    const rules = {}

    if (styleText) {
        styleText = tool.decodeContent(styleText)
        styleText = styleText.replace(/url\([^)]+\)/ig, all => all.replace(/;/ig, ':#||#:')) // åå¤çå¼éé¢çåå·
        styleText.split(';').forEach(rule => {
            rule = rule.trim()
            if (!rule) return

            const split = rule.indexOf(':')
            if (split === -1) return

            const name = toCamel(rule.substr(0, split).trim())
            rules[name] = rule.substr(split + 1).replace(/:#\|\|#:/ig, ';').trim()
        })
    }

    return rules
}

class Style {
    constructor(onUpdate) {
        this.$$init(onUpdate)
    }

    /**
     * åå§å style åè¡¨
     */
    static $$initStyleList(extraStyleList = []) {
        if (isInitStyleList) return

        isInitStyleList = true
        styleList = styleList.concat(extraStyleList)

        // è®¾ç½®åä¸ªå±æ§ç getterãsetter
        const properties = {}
        styleList.forEach(name => {
            properties[name] = {
                get() {
                    return this[`$_${name}`] || ''
                },
                set(value) {
                    // const config = cache.getConfig()
                    const oldValue = this[`$_${name}`]
                    value = value !== undefined ? '' + value : undefined

                    // å¤æ­ value æ¯å¦éè¦å å
                    // if (value && config.optimization.styleValueReduce && value.length > config.optimization.styleValueReduce) {
                    //     console.warn(`property "${name}" will be deleted, because it's greater than ${config.optimization.styleValueReduce}`)
                    //     value = undefined
                    // }

                    this[`$_${name}`] = value
                    if (oldValue !== value) this.$_checkUpdate()
                },
            }
        })
        Object.defineProperties(Style.prototype, properties)
    }

    /**
     * åå»ºå®ä¾
     */
    static $$create(onUpdate) {
        const config = cache.getConfig()

        if (config.optimization.domExtendMultiplexing) {
            // å¤ç¨ dom æ©å±å¯¹è±¡
            const instance = pool.get()

            if (instance) {
                instance.$$init(onUpdate)
                return instance
            }
        }

        return new Style(onUpdate)
    }

    /**
     * åå§åå®ä¾
     */
    $$init(onUpdate) {
        this.$_doUpdate = onUpdate || (() => {})
        this.$_disableCheckUpdate = false // æ¯å¦ç¦æ­¢æ£æ¥æ´æ°
        this.$__vars = null
    }

    /**
     * éæ¯å®ä¾
     */
    $$destroy() {
        this.$_doUpdate = null
        this.$_disableCheckUpdate = false
        this.$__vars = null

        styleList.forEach(name => {
            this[`$_${name}`] = undefined
        })
    }

    /**
     * åæ¶å®ä¾
     */
    $$recycle() {
        this.$$destroy()

        const config = cache.getConfig()

        if (config.optimization.domExtendMultiplexing) {
            // å¤ç¨ dom æ©å±å¯¹è±¡
            pool.add(this)
        }
    }

    /**
     * css åéå­å¨
     */
    get $_vars() {
        if (!this.$__vars) this.$__vars = {}
        return this.$__vars
    }

    /**
     * æ£æ¥æ´æ°
     */
    $_checkUpdate() {
        if (!this.$_disableCheckUpdate) {
            this.$_doUpdate()
        }
    }

    /**
     * å¯¹å¤å±æ§åæ¹æ³
     */
    get cssText() {
        let joinText = styleList
            .filter(name => this[`$_${name}`])
            .map(name => `${toDash(name)}:${this['$_' + name]}`)
            .join(';')
            .trim()
        joinText = joinText ? `${joinText};` : ''

        if (this.$__vars) {
            const vars = this.$_vars
            const varsText = Object
                .keys(vars)
                .filter(name => vars[name])
                .map(name => `${name}:${vars[name]}`)
                .join(';')
                .trim()
            joinText = varsText ? `${joinText}${varsText};` : joinText
        }

        return joinText
    }

    set cssText(styleText) {
        if (typeof styleText !== 'string') return

        // å½æ¢æåå¼å·åæåå¼å·æ¶ä¸è¿è¡æ¿æ¢
        if (!(styleText.indexOf('"') > -1 && styleText.indexOf('\'') > -1)) {
            styleText = styleText.replace(/"/g, '\'')
        }

        // è§£ææ ·å¼
        const rules = parse(styleText)

        this.$_disableCheckUpdate = true // å°æ¯æ¡è§åçè®¾ç½®åå¹¶ä¸ºä¸æ¬¡æ´æ°
        for (const name of styleList) {
            this.setProperty(name, rules[name])
        }
        this.$_disableCheckUpdate = false
        this.$_checkUpdate()
    }

    setProperty(name, value) {
        if (typeof name !== 'string') return ''

        if (name.indexOf('--') === 0) {
            this.$_vars[name] = value // css åé
            this.$_checkUpdate()
        } else this[name] = value
    }

    getPropertyValue(name) {
        if (typeof name !== 'string') return ''

        if (name.indexOf('--') === 0) return this.$_vars[name] || ''
        else return this[toCamel(name)] || ''
    }
}

module.exports = Style
