const Element = require('../element')
const Event = require('../../event/event')
const Pool = require('../../util/pool')
const cache = require('../../util/cache')

// eslint-disable-next-line no-var, block-scoped-var, semi
var $wx;

if (typeof $wx === 'undefined' && typeof my !== 'undefined') {
    // æ¯ä»å®ééé»è¾
    // eslint-disable-next-line no-undef
    $wx = my
} else {
    $wx = wx
}

const pool = new Pool()

class Image extends Element {
    /**
     * åå»ºå®ä¾
     */
    static $$create(options, tree) {
        const config = cache.getConfig()

        if (config.optimization.elementMultiplexing) {
            // å¤ç¨ element èç¹
            const instance = pool.get()

            if (instance) {
                instance.$$init(options, tree)
                return instance
            }
        }

        return new Image(options, tree)
    }

    /**
     * è¦åç¶ç±»ç $$init æ¹æ³
     */
    $$init(options, tree) {
        const width = options.width
        const height = options.height

        if (typeof width === 'number' && width >= 0) options.attrs.width = width
        if (typeof height === 'number' && height >= 0) options.attrs.height = height

        super.$$init(options, tree)

        this.$_naturalWidth = 0
        this.$_naturalHeight = 0

        this.$_initRect()
    }

    /**
     * è¦åç¶ç±»ç $$destroy æ¹æ³
     */
    $$destroy() {
        super.$$destroy()

        this.$_naturalWidth = null
        this.$_naturalHeight = null
    }

    /**
     * è¦åç¶ç±»çåæ¶å®ä¾æ¹æ³
     */
    $$recycle() {
        this.$$destroy()

        const config = cache.getConfig()

        if (config.optimization.elementMultiplexing) {
            // å¤ç¨ element èç¹
            pool.add(this)
        }
    }

    /**
     * æ´æ°ç¶ç»ä»¶æ 
     */
    $_triggerParentUpdate() {
        this.$_initRect()
        super.$_triggerParentUpdate()
    }

    /**
     * åå§åé¿å®½
     */
    $_initRect() {
        const width = this.$_attrs.get('width')
        const height = this.$_attrs.get('height')

        const widthNum = +width
        if (!isNaN(+widthNum) && +widthNum >= 0) this.$_style.width = `${width}px`
        else if (width && typeof width === 'string') this.$_style.width = width // å¯è½è®¾ç½® width="100%"

        const heightNum = +height
        if (!isNaN(+heightNum) && +heightNum >= 0) this.$_style.height = `${height}px`
        else if (height && typeof height === 'string') this.$_style.height = height // å¯è½è®¾ç½® width="100%"
    }

    /**
     * éç½®é¿å®½
     */
    $_resetRect(rect = {}) {
        this.$_naturalWidth = rect.width || 0
        this.$_naturalHeight = rect.height || 0

        this.$_initRect()
    }

    /**
     * å¯¹å¤å±æ§åæ¹æ³
     */
    get src() {
        return this.$_attrs.get('src') || ''
    }

    set src(value) {
        if (!value || typeof value !== 'string') return

        this.$_attrs.set('src', value)

        setTimeout(() => {
            $wx.getImageInfo({
                src: this.src,
                success: res => {
                    // å è½½æåï¼è°æ´å¾ççå®½é«
                    this.$_resetRect(res)

                    // è§¦å load äºä»¶
                    this.$$trigger('load', {
                        event: new Event({
                            name: 'load',
                            target: this,
                            eventPhase: Event.AT_TARGET
                        }),
                        currentTarget: this,
                    })
                },
                fail: () => {
                    // å è½½å¤±è´¥ï¼è°æ´å¾ççå®½é«
                    this.$_resetRect({width: 0, height: 0})

                    // è§¦å error äºä»¶
                    this.$$trigger('error', {
                        event: new Event({
                            name: 'error',
                            target: this,
                            eventPhase: Event.AT_TARGET
                        }),
                        currentTarget: this,
                    })
                },
            })
        }, 0)
    }

    get width() {
        return parseFloat(this.$_attrs.get('width'), 10) || 0
    }

    set width(value) {
        if (typeof value !== 'number' || !isFinite(value) || value < 0) return

        this.$_attrs.set('width', value)
        this.$_initRect()
    }

    get height() {
        return parseFloat(this.$_attrs.get('height'), 10) || 0
    }

    set height(value) {
        if (typeof value !== 'number' || !isFinite(value) || value < 0) return

        this.$_attrs.set('height', value)
        this.$_initRect()
    }

    get naturalWidth() {
        return this.$_naturalWidth
    }

    get naturalHeight() {
        return this.$_naturalHeight
    }
}

module.exports = Image
